
import AlertModule from 'vux/src/plugins/alert/module'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
name: "Notification",
components: {
Alert,
Cell,
Group,
XSwitch
},
methods: {
showModuleAuto (item) {
this.showModule(item)
},
showModule (item) {
AlertModule.show({
title: '提示',
content: this.$t('暂未完成该模块'),
onShow () {
console.log('Module: I\'m showing')
},
onHide () {
console.log('Module: I\'m hiding now')
}
})
}
}
}



